html,
body {
  height: 100%;
  background-color: rgba(192, 192, 192, 0.884) !important;
}
.Navbar {
  background-color: rgba(25, 8, 97, 0.952);
  border-bottom: 4px solid rgba(211, 29, 99, 0.863);
}
.Brand {
  animation-name: header;
  animation-duration: 0.7s;
  animation-delay: 1s;
  animation-iteration-count: infinite;
}
@keyframes header {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #ec34cdc4;
  }

  to {
    text-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #3517dbe0;
  }
}

.active {
  color: rgb(206, 140, 18) !important;
}
a:hover {
  color: white !important;
}
.Link {
  color: rgb(136, 141, 141);
}

.Footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(25, 8, 97, 0.952);
  color: white;
  text-align: center;
}
.Copy {
  background-color: rgba(21, 8, 97, 0.959);
  text-align: center;
  color: white;
}
.Title {
  color: rgb(14, 14, 56);
}
.DisplayDetails {
  padding-bottom: 100px;
}
